import React from 'react';
import s from './FeedbackOptions.module.css';
// import s from '../../sass/components/_FeedbackOptions.scss';

//  блок кнопок
class FeedbackOptions extends React.Component {
  render() {
    return (
      <div>
        <p>Please leave feedback</p>
        <button
          className={s.btn}
          onClick={() => {
            console.log('click');
          }}
        >
          Good
        </button>
        <button
          className={s.btn}
          onClick={() => {
            console.log('click');
          }}
        >
          Neutral
        </button>
        <button
          className={s.btn}
          onClick={() => {
            console.log('click');
          }}
        >
          Bad
        </button>
      </div>
    );
  }

  options = {};
  onLeaveFeedback = {};
}

export default FeedbackOptions;
